.bouncingTitleBox {
  z-index: 2;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
  padding: 0;
  width: 120px;
  height: 120px;
  position: absolute;
  -webkit-animation: horizontalMove 8s linear infinite alternate, verticalMove 5s linear infinite alternate;
          animation: horizontalMove 8s linear infinite alternate, verticalMove 5s linear infinite alternate;
  -webkit-animation-play-state: running;
          animation-play-state: running; }

.bouncingTitle {
  color: #330544;
  margin: 0;
  padding: 24px 0 0;
  font-size: 40px;
  line-height: 40px;
  max-height: 80px;
  font-family: 'Pinyon Script', cursive;
  text-align: center; }

@-webkit-keyframes verticalMove {
  0% {
    top: 10vh; }
  100% {
    top: calc(100vh - 120px); } }

@keyframes verticalMove {
  0% {
    top: 10vh; }
  100% {
    top: calc(100vh - 120px); } }

@-webkit-keyframes horizontalMove {
  0% {
    left: 0; }
  100% {
    left: calc(100vw - 120px); } }

@keyframes horizontalMove {
  0% {
    left: 0; }
  100% {
    left: calc(100vw - 120px); } }

@media (max-width: 500px) {
  .bouncingTitleBox {
    visibility: hidden; } }

@media (min-width: 1300px) {
  .bouncingTitleBox {
    -webkit-animation: horizontalMove 12s linear infinite alternate, verticalMove 8s linear infinite alternate;
            animation: horizontalMove 12s linear infinite alternate, verticalMove 8s linear infinite alternate;
    width: 160px;
    height: 160px; }
  .bouncingTitle {
    font-size: 53.33333px;
    line-height: 53.33333px;
    max-height: 120px;
    padding: 32px 0 0; }
  @-webkit-keyframes verticalMove {
    0% {
      top: 10vh; }
    100% {
      top: calc(100vh - 160px); } }
  @keyframes verticalMove {
    0% {
      top: 10vh; }
    100% {
      top: calc(100vh - 160px); } }
  @-webkit-keyframes horizontalMove {
    0% {
      left: 0; }
    100% {
      left: calc(100vw - 160px); } }
  @keyframes horizontalMove {
    0% {
      left: 0; }
    100% {
      left: calc(100vw - 160px); } } }



