.showsPage {
  overflow-y: scroll;
  height: 90vh; }

.showsPage div {
  min-height: 240px; }

.showTypeHeading {
  text-align: center;
  padding: 8px;
  margin: 8px auto;
  font-size: 2em;
  font-family: 'Macondo Swash Caps', cursive;
  display: inline-block;
  border-radius: 8px;
  -webkit-box-shadow: 1px 1px 10px black;
          box-shadow: 1px 1px 10px black; }

.upcomingHeading {
  background: #d93;
  color: whitesmoke;
  border: 2px solid #b71; }

.previousHeading {
  background: black;
  color: whitesmoke;
  border: 2px solid #111; }

@media (max-width: 700px) {
  .showTypeHeading {
    font-size: 1.7em; } }



