html,
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow: hidden; }

ol,
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0; }

a {
  text-decoration: none;
  cursor: inherit;
  color: inherit; }

.page {
  height: 90vh; }

.navContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  height: 10vh;
  max-height: 10vh;
  border-radius: 3px;
  border: 2px double maroon;
  -webkit-box-shadow: 1px 1px 2px grey;
          box-shadow: 1px 1px 2px grey; }

.navLink {
  z-index: 2;
  -webkit-box-shadow: 2px 2px 2px gold;
          box-shadow: 2px 2px 2px gold;
  background: violet;
  border: 1px dotted purple;
  border-radius: 3px;
  padding: 4px;
  height: 24px;
  color: lightgreen;
  -webkit-animation: colorChange 1s linear infinite forwards;
          animation: colorChange 1s linear infinite forwards;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  text-align: center; }
  .navLink p {
    font-size: 20px;
    margin: 0;
    text-shadow: 2px 2px 1px black; }

.normalNav:hover {
  cursor: help;
  -webkit-animation-play-state: running;
          animation-play-state: running; }
  .normalNav:hover p {
    font-size: 25px !important;
    margin: -5px 0;
    -webkit-text-decoration: underline wavy;
            text-decoration: underline wavy; }

#moreCat {
  display: block;
  -webkit-animation: navDrop 0.5s ease-in infinite forwards;
          animation: navDrop 0.5s ease-in infinite forwards;
  -webkit-animation-play-state: paused;
          animation-play-state: paused; }

#moreCat:hover {
  -webkit-animation-play-state: running;
          animation-play-state: running;
  cursor: help; }

@-webkit-keyframes navDrop {
  0% {
    margin-top: 0px; }
  100% {
    margin-top: 100px; } }

@keyframes navDrop {
  0% {
    margin-top: 0px; }
  100% {
    margin-top: 100px; } }

@-webkit-keyframes colorChange {
  0% {
    color: lightgreen; }
  10% {
    color: darkorchid; }
  20% {
    color: darkcyan; }
  30% {
    color: violet; }
  40% {
    color: goldenrod; }
  50% {
    color: saddlebrown; }
  60% {
    color: crimson; }
  70% {
    color: blanchedalmond; }
  80% {
    color: darkolivegreen; }
  90% {
    color: lightskyblue; }
  100% {
    color: lightgreen; } }

@keyframes colorChange {
  0% {
    color: lightgreen; }
  10% {
    color: darkorchid; }
  20% {
    color: darkcyan; }
  30% {
    color: violet; }
  40% {
    color: goldenrod; }
  50% {
    color: saddlebrown; }
  60% {
    color: crimson; }
  70% {
    color: blanchedalmond; }
  80% {
    color: darkolivegreen; }
  90% {
    color: lightskyblue; }
  100% {
    color: lightgreen; } }

@media (max-width: 500px) {
  .navLink {
    height: 20px; }
    .navLink p {
      font-size: 16px; }
  .normalNav:hover p {
    font-size: 22px !important; } }

@media (min-width: 900px) {
  .navLink {
    height: 28px;
    padding: 6px 8px; }
    .navLink p {
      font-size: 24px; }
  .normalNav:hover p {
    font-size: 32px !important; } }



