.showList {
  text-align: center; }

.showListBox {
  margin: 5px auto;
  border: 2px solid black;
  padding: 8px 4px;
  display: inline-block;
  background: -webkit-gradient(linear, left top, left bottom, from(purple), color-stop(maroon), to(black));
  background: -webkit-linear-gradient(purple, maroon, black);
  background: -o-linear-gradient(purple, maroon, black);
  background: linear-gradient(purple, maroon, black);
  border-radius: 4px;
  -webkit-box-shadow: 1px 1px 1px black;
          box-shadow: 1px 1px 1px black; }

.showListItemRow {
  font-family: 'Rubik' sans-serif;
  border-radius: 2px;
  color: goldenrod; }
  .showListItemRow strong {
    color: gold; }

.showListLink {
  color: hotpink; }

.showListLink:hover {
  background: black;
  cursor: help;
  color: pink; }



