.alertTicker {
  position: relative;
  -webkit-animation: scroll 30s linear infinite forwards;
          animation: scroll 30s linear infinite forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  white-space: nowrap;
  padding: 4px;
  z-index: 10;
  font-size: 1.5rem;
  background: black;
  width: initial;
  margin: 0; }

@-webkit-keyframes scroll {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    color: red; }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    color: yellow; } }

@keyframes scroll {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    color: red; }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    color: yellow; } }



